.card {
  margin: 3px;
  border-radius: 30px;
  color: white;
  transition: 1s;
}
.card:hover {
  background-color: rgba(0, 173, 181, 0.2);
}
h3 {
  margin-bottom: 5px;
  padding-left: 5px;
}

p {
  text-align: justify;
  padding-left: 5px;
}

img {
  width: 150px;
  height: 150px;
  border-radius: 30px;
}
